.channelRow{
    display: flex;
    /* justify-content: center; */
    align-items: center;
    width: 70%;
}
.channelRow__logo{
    width: 120px !important;
    height: 120px !important;
    margin: 10px 60px;
}
.channelRow__text {
    display: flex;
    flex-direction: column;  
}
.channelRow__text > p{
    color: gray;
    font-size: small !important;
}
.channelRow__text > h4{
    display: flex;
    align-items: center;
    justify-content: space-between;
}